// services section

.stripe {
    border-top: 1px solid #CFCFFB;
    border-bottom: 1px solid #CFCFFB;
}

.services {
    background: $primary;

    &_list {
        padding: 60px 0;
        display: flex;
        flex-direction: column;
        gap: 30px 0;
    }
}

// media queries

@media screen and (min-width: 767.98px) {
    .services {

        &_list {
            flex-direction: row;
            flex-wrap: wrap;
            gap: 30px;
            padding: 100px 0;

            .wrapper {
                width: calc(50% - 20px);
            }

            &-item {
                height: 100%;
            }
        }
    }
}

@media screen and (min-width: 991.98px) {
    .services {
        &_list {
            gap: 50px;
            padding: 120px 0;

            .wrapper {
                width: calc(50% - 25px);
            }
        }
    }
}

@media screen and (min-width: 1199.98px) {
    .services {
        &_list {
            gap: 0;
            flex-wrap: nowrap;

            .wrapper {
                width: 25%;
            }

            &-item {
                padding-right: 10px;
            }
        }
    }
}