// challenges section

.challenges {
    background: $lightgray;

    &_header {
        &-title {
            color: $primary;
        }
    }

    &_list {
        &-item {
            .separator {
                background: $headerColor;
            }
        }
    }
}

// media queries

@media screen and (min-width: 1399.98px) {
    .challenges {
        &_shapes {
            .half {
                .circle {
                    background: #fff;
                    border: $border;
                }

                .shape {
                    position: absolute;
                    width: auto;
                }

                &--left {
                    .circle {
                        width: 140px;
                        height: 140px;
                        left: -54px;
                        top: -41px;
                    }

                    .shape {
                        max-height: 277px;
                        bottom: -60px;
                        left: -30px;
                    }
                }

                &--right {
                    .shape {
                        max-height: 268px;
                        right: -30px;
                        top: -80px;
                    }

                    .circle {
                        &--small {
                            width: 20px;
                            height: 20px;
                            top: 90px;
                            right: 300px;
                        }

                        &--big {
                            width: 50px;
                            height: 50px;
                            right: 120px;
                            bottom: 280px;
                        }
                    }
                }
            }
        }
    }
}